:root{
  --color-one:#f4ca42;
  --color-two:#4fd1d1;
  --bg-color:#fff1c6;
}

.App {
    

}
.header-title{
  background: var(--color-two);
   box-shadow: 0px 4px 0px var(--color-one);
}
.scrollbar{
    overflow-y:scroll;
    overflow-x:hidden;
}

#style-1::-webkit-scrollbar {
    width: 0px;
} 

.icon i {
 font-size:4rem;
 margin:20px;
 padding:10px;
}

.backdrop {
	position:fixed;
	width:100%;
	height:100%;
	top:0;
	left:0;
	background:rgba(0,0,0,0.4);
	z-index:100;
}

.shadowE{
  box-shadow:0px 15px 10px -15px rgba(0,0,0,0.5);

}
/**************
Link Effect
************/
a,.btnBtn {
  transition: all 500ms cubic-bezier(0.400, 0.500, 0.160, 1.300);
  -webkit-transition: all 500ms cubic-bezier(0.400, 0.500, 0.160, 1.300);
  -moz-transition: all 500ms cubic-bezier(0.400, 0.500, 0.160, 1.300);
  -ms-transition: all 500ms cubic-bezier(0.400, 0.500, 0.160, 1.300);
  -o-transition: all 500ms cubic-bezier(0.400, 0.500, 0.160, 1.300);
  display: block;
}

a.linkBtn:hover , .btnBtn:hover{
  color:white;
  box-shadow: var(--color-one) 0 0px 0px 30px inset;
}
