body {
  margin: 0;
  padding: 0;
  font-family:"raleway", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.side-nav{
	padding:30px;
	height:400px;
	background:linear-gradient(to bottom ,var(--color-one), white);
	box-shadow:1px 0px 7px rgba(0,0,0,0.5);
	position:fixed;
	top:0;
	right:0;
	width:70%;
	max-width:400px;
	width:350px;
	z-index:200;
	transition: ease-out, -webkit-transform 0.5s;
	transition: transform 0.5s, ease-out;
	transition: transform 0.5s, ease-out, -webkit-transform 0.5s;
	border-bottom-left-radius: 400px;
}
.hide{
	-webkit-transform:translateX(+100%);
	        transform:translateX(+100%);
}

.bar{
	position:fixed;
	z-index:200;
	top:10px;
	right:10px;
}
.bar-line{
  width: 30px;
  height: 3px;
  background-color: rgb(0,0,0,.7);
  margin: 6px 0;
}

.myInitial{
	position:absolute;
	z-index:200;
	top:10px;
	left:10px;
}



.frontBg{
  background-image: linear-gradient(to bottom, var(--color-one), white);
}
.name-style{
	font-size:74px;
}

.greenC{
  color:var(--color-two);

}
.yellowC{
  color:var(--color-one);
}
